import Vue from 'vue'
import Router from 'vue-router'
import store from '../store/store';

Vue.use(Router)


const requireAuth = (to, from, next) => {

   if(store.state.UserRoleId == 'coach'){
      next('/login')
   }else if(store.state.UserRoleId == 'student'){
      next('/student')
   }else{
      next('/login')
   }
}



